.register{           
    border-radius: 1em 1em 1em 1em;    
    border-width: 1px;
    box-shadow: rgba(13, 250, 165, .8) 0px 0px 250px;
    color: #333;
    border-color: rgba(0, 0, 0, .1);
    margin-top: 3rem;
    margin-bottom: 2rem;   
    max-width: 35em;
    margin-right: auto;
    margin-left: auto;
    margin-top: 8em;    
    background-color: rgba(0, 211, 233, 0.05);
    background-image: linear-gradient(160deg, rgba(31, 213, 250, 0.05) 20%, #80D0C7 100%);
    padding:2em;    
}

@media screen and (max-width: 600px){
    .register{           
       transform: scale(.75);
       margin-top: 2em;
    }
    .mobile{
        margin-top: 1em;
        width: 200px;
    }
}